.main-title {
    /* font-family: 'Amaranth', sans-serif; */
    font-family: 'Cabin Sketch', cursive;
    font-size: 4.8em;
}

.home-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/800x600');
    /* background-image: url('https://source.unsplash.com/LLcAJbEh1_Q/800x600'); */
}

.search-container {
    display: flex;
    align-items: center;
}

.search-box-container {
    margin: 1.5em;
}

.home-search-box {
    height: 2.5em;
}

.search-button {
    padding: 0.5em 0.7em;
    /* margin: 1em; */
    border-radius: 0.5em;
    font-size: 1.4em;
    background-color: var(--dark-button-color);
    border: none;
}

.buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-button {
    padding: 1em 2em;
    margin: 1em;
    border-radius: 0.5em;
    background-color: var(--light-button-color);
    font-size: 1.4em;
    font-weight: bold;
    border: none;
}

@media(min-width: 768px) {
    .home-screen {
        background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/1200x1000');
    }
}

@media(min-width: 1200px) {
    .home-screen {
        background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/1400x600');
    }
}
.map-container {
    width: 85%;
    height: 300px;
    margin: auto;
}

.search-input-container {
    display: flex;
    justify-content: space-evenly;
    margin: 1rem;
}

.search-box {
    box-sizing: border-box;
    border: 1 px solid transparent;
    width: 200px;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    font-size: 12px;
    outline: none;
    text-overflow: ellipses;
    padding: 2px 10px;
}

.form-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* background-image: url('https://source.unsplash.com/LLcAJbEh1_Q/800x600');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; */
}

.form-title {
    margin: .5em 0 0 0;
    font-size: 3rem;
    font-family: 'Cabin Sketch', cursive;
    text-align: center;
}

.add-item-form {
    width: 300px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    margin: 2.5em auto;
    align-items: center;
    border: 2px solid var(--dark-button-color);
    background-color: var(--light-button-color);
    border-radius: 1em;
    overflow-y: scroll;
}

.form-section-title {
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
    margin: .5em;
}

.form-submit-button {
    background-color: var(--dark-button-color);
    font-size: 1.7rem;
    padding: .5em;
    width: 10rem;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .8em;
}

.input-box {
    background-color: white;
    box-sizing: border-box;
    border: 1 px solid transparent;
    width: 200px;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(255, 149, 11, 0.3);
    font-size: 12px;
    outline: none;
    text-overflow: ellipses;
    padding: 2px 10px;

}

.form-section {
    display: flex;
    flex-direction: column;
    width: 75%;
    align-items: center;
    margin: 1em auto
}

.description-box {
    height: 70px;
}

@media(min-width: 768px) {
    .add-item-form {
        width: 500px;
    }
    .form-title {
        font-size: 4rem;
    }
    .form-section-title {
        font-size: 2.5rem;
    }
    .input-box {
        font-size: 2rem;
        width: 90%;
    }
    .form-submit-button {
        font-size: 2.5rem;
        width: 15rem;
    }
    .description-box {
        height: 100px;
    }
}
